/* http://meyerweb.com/eric/tools/css/reset/
   v2.0-modified | 20110126
   License: none (public domain)
*/
html,
body,
div,
span,
applet,
object,
iframe,
h1,
h2,
h3,
h4,
h5,
h6,
p,
blockquote,
pre,
a,
abbr,
acronym,
address,
big,
cite,
code,
del,
dfn,
em,
img,
ins,
kbd,
q,
s,
samp,
small,
strike,
strong,
sub,
sup,
tt,
var,
b,
u,
i,
center,
dl,
dt,
dd,
ol,
ul,
li,
fieldset,
form,
label,
legend,
table,
caption,
tbody,
tfoot,
thead,
tr,
th,
td,
article,
aside,
canvas,
details,
embed,
figure,
figcaption,
footer,
header,
hgroup,
menu,
nav,
output,
ruby,
section,
summary,
time,
mark,
audio,
video {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  font: inherit;
  vertical-align: baseline;
}

/* make sure to set some focus styles for accessibility */
:focus {
  outline: 0;
}

/* HTML5 display-role reset for older browsers */
article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
menu,
nav,
section {
  display: block;
}

body {
  line-height: 1;
}

ol,
ul {
  list-style: none;
}

blockquote,
q {
  quotes: none;
}

blockquote:before,
blockquote:after,
q:before,
q:after {
  content: "";
  content: none;
}

table {
  border-collapse: collapse;
  border-spacing: 0;
}

input[type=search]::-webkit-search-cancel-button,
input[type=search]::-webkit-search-decoration,
input[type=search]::-webkit-search-results-button,
input[type=search]::-webkit-search-results-decoration {
  -webkit-appearance: none;
  -moz-appearance: none;
}

input[type=search] {
  -webkit-appearance: none;
  -moz-appearance: none;
  -webkit-box-sizing: content-box;
  -moz-box-sizing: content-box;
  box-sizing: content-box;
}

textarea {
  overflow: auto;
  vertical-align: top;
  resize: vertical;
}

/**
 * Correct `inline-block` display not defined in IE 6/7/8/9 and Firefox 3.
 */
audio,
canvas,
video {
  display: inline-block;
  *display: inline;
  *zoom: 1;
  max-width: 100%;
}

/**
 * Prevent modern browsers from displaying `audio` without controls.
 * Remove excess height in iOS 5 devices.
 */
audio:not([controls]) {
  display: none;
  height: 0;
}

/**
 * Address styling not present in IE 7/8/9, Firefox 3, and Safari 4.
 * Known issue: no IE 6 support.
 */
[hidden] {
  display: none;
}

/**
 * 1. Correct text resizing oddly in IE 6/7 when body `font-size` is set using
 *    `em` units.
 * 2. Prevent iOS text size adjust after orientation change, without disabling
 *    user zoom.
 */
html {
  font-size: 100%;
  /* 1 */
  -webkit-text-size-adjust: 100%;
  /* 2 */
  -ms-text-size-adjust: 100%;
  /* 2 */
}

/**
 * Address `outline` inconsistency between Chrome and other browsers.
 */
a:focus {
  outline: thin dotted;
}

/**
 * Improve readability when focused and also mouse hovered in all browsers.
 */
a:active,
a:hover {
  outline: 0;
}

/**
 * 1. Remove border when inside `a` element in IE 6/7/8/9 and Firefox 3.
 * 2. Improve image quality when scaled in IE 7.
 */
img {
  border: 0;
  /* 1 */
  -ms-interpolation-mode: bicubic;
  /* 2 */
}

/**
 * Address margin not present in IE 6/7/8/9, Safari 5, and Opera 11.
 */
figure {
  margin: 0;
}

/**
 * Correct margin displayed oddly in IE 6/7.
 */
form {
  margin: 0;
}

/**
 * Define consistent border, margin, and padding.
 */
fieldset {
  border: 1px solid #c0c0c0;
  margin: 0 2px;
  padding: 0.35em 0.625em 0.75em;
}

/**
 * 1. Correct color not being inherited in IE 6/7/8/9.
 * 2. Correct text not wrapping in Firefox 3.
 * 3. Correct alignment displayed oddly in IE 6/7.
 */
legend {
  border: 0;
  /* 1 */
  padding: 0;
  white-space: normal;
  /* 2 */
  *margin-left: -7px;
  /* 3 */
}

/**
 * 1. Correct font size not being inherited in all browsers.
 * 2. Address margins set differently in IE 6/7, Firefox 3+, Safari 5,
 *    and Chrome.
 * 3. Improve appearance and consistency in all browsers.
 */
button,
input,
select,
textarea {
  font-size: 100%;
  /* 1 */
  margin: 0;
  /* 2 */
  vertical-align: baseline;
  /* 3 */
  *vertical-align: middle;
  /* 3 */
}

/**
 * Address Firefox 3+ setting `line-height` on `input` using `!important` in
 * the UA stylesheet.
 */
button,
input {
  line-height: normal;
}

/**
 * Address inconsistent `text-transform` inheritance for `button` and `select`.
 * All other form control elements do not inherit `text-transform` values.
 * Correct `button` style inheritance in Chrome, Safari 5+, and IE 6+.
 * Correct `select` style inheritance in Firefox 4+ and Opera.
 */
button,
select {
  text-transform: none;
}

/**
 * 1. Avoid the WebKit bug in Android 4.0.* where (2) destroys native `audio`
 *    and `video` controls.
 * 2. Correct inability to style clickable `input` types in iOS.
 * 3. Improve usability and consistency of cursor style between image-type
 *    `input` and others.
 * 4. Remove inner spacing in IE 7 without affecting normal text inputs.
 *    Known issue: inner spacing remains in IE 6.
 */
button,
html input[type=button],
input[type=reset],
input[type=submit] {
  -webkit-appearance: button;
  /* 2 */
  cursor: pointer;
  /* 3 */
  *overflow: visible;
  /* 4 */
}

/**
 * Re-set default cursor for disabled elements.
 */
button[disabled],
html input[disabled] {
  cursor: default;
}

/**
 * 1. Address box sizing set to content-box in IE 8/9.
 * 2. Remove excess padding in IE 8/9.
 * 3. Remove excess padding in IE 7.
 *    Known issue: excess padding remains in IE 6.
 */
input[type=checkbox],
input[type=radio] {
  box-sizing: border-box;
  /* 1 */
  padding: 0;
  /* 2 */
  *height: 13px;
  /* 3 */
  *width: 13px;
  /* 3 */
}

/**
 * 1. Address `appearance` set to `searchfield` in Safari 5 and Chrome.
 * 2. Address `box-sizing` set to `border-box` in Safari 5 and Chrome
 *    (include `-moz` to future-proof).
 */
input[type=search] {
  -webkit-appearance: textfield;
  /* 1 */
  -moz-box-sizing: content-box;
  -webkit-box-sizing: content-box;
  /* 2 */
  box-sizing: content-box;
}

/**
 * Remove inner padding and search cancel button in Safari 5 and Chrome
 * on OS X.
 */
input[type=search]::-webkit-search-cancel-button,
input[type=search]::-webkit-search-decoration {
  -webkit-appearance: none;
}

/**
 * Remove inner padding and border in Firefox 3+.
 */
button::-moz-focus-inner,
input::-moz-focus-inner {
  border: 0;
  padding: 0;
}

/**
 * 1. Remove default vertical scrollbar in IE 6/7/8/9.
 * 2. Improve readability and alignment in all browsers.
 */
textarea {
  overflow: auto;
  /* 1 */
  vertical-align: top;
  /* 2 */
}

/**
 * Remove most spacing between table cells.
 */
table {
  border-collapse: collapse;
  border-spacing: 0;
}

html,
button,
input,
select,
textarea {
  color: #222;
}

::-moz-selection {
  background: #b3d4fc;
  text-shadow: none;
}

::selection {
  background: #b3d4fc;
  text-shadow: none;
}

img {
  vertical-align: middle;
}

fieldset {
  border: 0;
  margin: 0;
  padding: 0;
}

textarea {
  resize: vertical;
}

.chromeframe {
  margin: 0.2em 0;
  background: #ccc;
  color: #000;
  padding: 0.2em 0;
}

/* header and navigation */
.panel {
  box-shadow: 0 3px 6px #00000029;
  position: sticky;
  position: -webkit-sticky;
  top: 0;
  display: flex;
  justify-content: space-between;
  align-items: center;
  z-index: 2;
}

.navigation {
  display: flex;
  justify-content: space-around;
  align-items: center;
}

.text--logo,
.footer__link--logo {
  text-align: left;
  font: normal normal 300 2em/2.1em "Raleway", sans-serif;
  letter-spacing: 0;
  color: #08A6E4;
  padding-left: 40px;
}

.list {
  display: flex;
  text-align: center;
  justify-content: space-around;
}

.list__element,
.list__link {
  color: #A5A5A5;
  letter-spacing: 3.28px;
  font: normal normal bold 1em/1.5em "Open Sans", sans-serif;
  padding: 30px 30px 30px 20px;
  border-bottom: 4px solid transparent;
}

.list__link:hover {
  color: black;
  border-color: #08A6E4;
}

.menu-burger {
  width: 60px;
  height: 60px;
  display: flex;
  justify-content: center;
  align-items: center;
  background: none;
  flex-direction: column;
  border: 0;
  cursor: pointer;
  display: none;
}
.menu-burger span {
  width: 25px;
  height: 4px;
  background: #A5A5A5;
  margin: 1px;
}

@media (max-width: 960px) {
  .panel {
    flex-direction: column;
  }

  .navigation {
    position: absolute;
    left: 0;
    top: -300;
    height: 100%;
    width: 250px;
    transform: translate(-90%);
    transition: 1s transform;
  }

  .navigation.is-show {
    transform: translate(0);
  }

  .list {
    flex-direction: column;
    margin: 0;
    position: absolute;
    top: 70px;
    left: 0;
    background-color: white;
    border-bottom: rgba(7, 172, 230, 0.16);
    color: #A5A5A5;
    text-align: left;
  }

  .list__link:hover {
    color: black;
    border: none;
  }

  .panel {
    flex-direction: row;
    position: relative;
  }

  .menu-burger {
    display: flex;
  }

  .list__element {
    padding: 10px;
    margin: 0px;
    text-align: left;
    font: normal normal bold 11px/15px Open Sans;
    letter-spacing: 3.28px;
    color: #000000;
  }
}
* {
  box-sizing: border-box;
  scroll-behavior: smooth;
}

body {
  font-family: var(--font-open-sans);
}

a {
  text-decoration: none;
}

.text--logo,
.footer__link--logo {
  text-align: left;
  font: normal normal 300 2em/2.1em "Raleway", sans-serif;
  letter-spacing: 0;
  color: #08A6E4;
}

h2 {
  font: normal normal normal 2.25rem "Raleway", sans-serif;
  letter-spacing: 0;
  color: #000000;
}

.container {
  width: 1352px;
  margin: 0 auto;
}
@media (max-width: 1352px) {
  .container {
    width: 100%;
    padding: 15px;
  }
}

/* opening */
.opening {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  overflow: hidden;
}

.opening__header {
  text-align: center;
  font: normal normal normal 3rem "Raleway", sans-serif;
  letter-spacing: 0;
  color: #000000;
  padding: 50px 0 20px 0;
}

.opening__text {
  text-align: center;
  font: normal normal normal 1rem "Open Sans", sans-serif;
  letter-spacing: 0px;
  color: #000000;
  padding-bottom: 38px;
}

.opening__link {
  background: #08A6E4 0% 0% no-repeat padding-box;
  box-shadow: 5px 10px 14px #07ACE629;
  border-radius: 35px;
  text-align: center;
  font: normal normal bold 1.2em Open Sans;
  letter-spacing: 0px;
  color: #FFFFFF;
  padding: 20px 50px 20px 50px;
  margin-top: 20px;
}

@media (max-width: 960px) {
  .opening {
    margin-top: 100px;
  }

  .opening__text {
    padding: 0 70px;
  }

  .opening__image {
    width: 220%;
  }
}
.button {
  color: white;
  font-weight: bold;
  display: flex;
  border: none;
  justify-content: center;
  align-items: center;
  border-radius: 35px;
}

@media (max-width: 960px) {
  .button {
    margin: 0 15px;
    padding: 16px 0;
    display: block;
  }
}
/* statistics */
.statistics {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.statistics__columns {
  display: flex;
  flex-direction: row;
  justify-content: center;
}

.statistics__header {
  text-align: center;
  font: normal normal normal 2rem "Raleway", sans-serif;
  letter-spacing: 0;
  color: #000000;
  padding: 100px 0 101px 0;
}

.statistics__column {
  display: flex;
  flex-direction: column;
  margin: 0 80px 149px 80px;
  align-items: center;
  text-align: center;
  font: normal normal normal 1.2rem "Raleway", sans-serif;
  letter-spacing: 0;
  color: #A5A5A5;
  justify-content: end;
  position: relative;
}

.statistics__column span {
  text-align: center;
  font: normal normal 800 1.2rem "Raleway", sans-serif;
  letter-spacing: 5.96px;
  color: #000000;
  margin: 14px 0 19px 0;
}

.statistics__image {
  height: 62.71px;
  width: 62.71px;
  position: absolute;
  display: flex;
  align-items: center;
}

.statistics__image--1 {
  left: 65px;
  top: 5px;
}

.statistics__image--2 {
  left: 105px;
  top: 2px;
}

.statistics__image--3 {
  left: 65px;
  top: 5px;
}

.statistics__circle {
  height: 62.71px;
  width: 62.71px;
  border: transparent solid 5px;
  overflow: visible;
  border-radius: 50%;
}

.statistics__circle--1 {
  background-color: #FB3B64;
}

.statistics__circle--2 {
  background-color: #08A6E4;
}

.statistics__circle--3 {
  background-color: #55DFB4;
}

@media (max-width: 960px) {
  .statistics {
    margin: 50px 35px auto 50px;
    align-items: center;
    flex-direction: column;
  }

  .statistics__columns {
    flex-direction: column;
  }

  .statistics__header {
    padding: 20px 0 30px 0;
  }

  .statistics__column {
    margin: 0 80px 30px 80px;
  }

  .statistics__column span {
    text-align: center;
    font: normal normal 800 1.2rem "Raleway", sans-serif;
    letter-spacing: 5.96px;
    color: #000000;
    margin: 14px 0 19px 0;
  }

  .statistics__image--1 {
    left: 70px;
    top: -4px;
  }

  .statistics__image--2 {
    left: 70px;
    top: 2px;
  }

  .statistics__image--3 {
    left: 70px;
    top: -3px;
  }

  .statistics__circle {
    height: 62.71px;
    width: 62.71px;
    border: transparent solid 5px;
    overflow: visible;
    border-radius: 50%;
  }
}
/* benefits */
.benefits {
  display: flex;
  flex-direction: column;
  align-items: center;
  background-color: #F7F7F7;
}

.benefits__row {
  width: 80%;
  background-color: white;
  display: flex;
  justify-content: space-between;
}

.benefits__row--1 {
  margin-top: 105px;
  border: 1px solid #E8E9ED;
}

.benefits__row--2 {
  margin: 37px 0 37px 0;
  border: 1px solid #E8E9ED;
}

.benefits__row--3 {
  margin-bottom: 105px;
}

.side__left--1 {
  display: flex;
  flex-direction: column;
  align-items: start;
  padding-left: 35px;
  width: 50%;
  background-color: white;
}

.row3__part--1 {
  display: flex;
  flex-direction: column;
  align-items: start;
  padding-left: 35px;
  width: 50%;
  background-color: white;
}

h2 {
  font: normal normal normal 2rem "Raleway", sans-serif;
  letter-spacing: 0;
  color: #000000;
}

.benefits__header--1,
.benefits__header--2,
.benefits__header--3 {
  margin: 4rem 0px 36px 0px;
  text-align: left;
  font: normal normal normal 2rem "Raleway", sans-serif;
  letter-spacing: 0px;
}

.benefits__text--1,
.benefits__text--2,
.benefits__text--3 {
  width: 60%;
  background-color: white;
  text-align: left;
  font: normal normal normal 1.2rem "Open Sans", sans-serif;
  letter-spacing: 0px;
}

.benefits__text--1::after {
  content: "";
  width: 330px;
  height: 4px;
  background-color: #FB3B64;
  display: block;
  margin-top: 50px;
  margin-bottom: 50px;
}

.benefits__text--2::after {
  content: "";
  width: 330px;
  height: 4px;
  background-color: #08A6E4;
  display: block;
  margin-top: 50px;
  margin-bottom: 50px;
}

.benefits__text--3::after {
  content: "";
  width: 330px;
  height: 4px;
  background-color: #55DFB4;
  display: block;
  margin-top: 50px;
  margin-bottom: 50px;
}

.side__right--1 {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 50%;
}

.trumpet {
  width: 60%;
}

.macbook {
  width: 70%;
  margin-top: 50px;
  z-index: 1;
}

.shape__row1 {
  background-color: #FB3B64;
  border-radius: 41px;
  transform: matrix(0.71, 0.71, -0.71, 0.71, 0, 0);
  width: 180px;
  height: 180px;
  position: relative;
  bottom: 50px;
  right: 150px;
}

.side__left--2 {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 50%;
}

.side__right--2 {
  display: flex;
  flex-direction: column;
  align-items: start;
  padding-left: 35px;
  width: 50%;
}

.iphone {
  width: 150%;
  z-index: 1;
}

.shape__row2 {
  background: #07ACE6 0% 0% no-repeat padding-box;
  width: 320px;
  height: 320px;
  border-radius: 50%;
  position: relative;
  right: 100px;
}

.row3__part--1 {
  background-color: white;
  width: 55%;
  border: 1px solid #E8E9ED;
}

.row3__part--2 {
  background-color: #F7F7F7;
  width: 5%;
}

.row3__part--3 {
  background-color: #55DFB4;
  width: 40%;
  display: flex;
  justify-content: center;
  align-items: center;
  border: 1px solid #E8E9ED;
}

@media (max-width: 960px) {
  .benefits {
    padding: 50px 0;
    flex-direction: column;
  }

  .benefits__row--1 {
    flex-direction: column-reverse;
    justify-content: center;
  }

  .benefits__row--2,
.benefits__row--3 {
    flex-direction: column;
    justify-content: center;
  }

  .side {
    margin: 0 auto;
    width: 100%;
  }

  .benefits__text--1::after,
.benefits__text--2::after,
.benefits__row--3::after {
    content: "";
    width: 330px;
    height: 4px;
    background-color: #FB3B64;
    display: block;
    margin-top: 50px;
    display: none;
  }

  .benefits__header--1,
.benefits__header--2,
.benefits__header--3 {
    margin: 20px 0 36px 0;
    text-align: left;
    font: normal normal normal 2rem "Raleway", sans-serif;
    letter-spacing: 0;
  }

  .benefits__header--2 {
    margin-top: 0;
  }

  .benefits__text--1,
.benefits__text--2,
.benefits__text--3 {
    width: 90%;
    text-align: left;
    font: normal normal normal 1rem "Open Sans", sans-serif;
    letter-spacing: 0px;
    margin-bottom: 50px;
  }

  .side__right--1 {
    width: 100%;
    justify-content: center;
    position: relative;
  }
  .side__right--1 .macbook {
    justify-content: center;
    z-index: 1;
  }

  .shape__row1 {
    background-color: #FB3B64;
    border-radius: 35px;
    transform: matrix(0.71, 0.71, -0.71, 0.71, 0, 0);
    width: 90px;
    height: 90px;
    position: absolute;
    bottom: 40px;
    right: 40px;
    margin: 0 auto;
    display: none;
  }

  .iphone {
    width: 100%;
    vertical-align: baseline;
  }

  .side__left--2 {
    width: 90%;
    justify-items: center;
    display: block;
    margin: 0 auto;
  }
  .side__left--2 .shape__row2 {
    background: #07ACE6 0% 0% no-repeat padding-box;
    border-radius: 50%;
    transform: scale(0.6, 0.6);
    position: static;
    margin: 0 auto;
    width: auto;
    height: auto;
  }

  .row3__part--1 {
    width: 100%;
  }

  .row3__part--3 {
    width: 100%;
  }

  .benefits__text--3::after {
    content: "";
    width: 330px;
    height: 4px;
    background-color: #55DFB4;
    display: block;
    margin-top: 50px;
    display: none;
  }
}
/* pricing */
.pricing {
  margin: 100px auto;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.pricing__header {
  text-align: center;
  font: normal normal normal 2.25rem "Raleway", sans-serif;
  letter-spacing: 0px;
  color: #000000;
}

.pricing__columns {
  display: flex;
  align-items: center;
  justify-content: center;
  margin-top: 50px;
}

.pricing__column {
  width: 370px;
  border-radius: 18px;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.pricing__top {
  margin-top: 34px;
  margin-bottom: 60px;
  text-align: center;
}

.pricing__plan {
  font-size: 1.22rem;
  font-family: "Raleway", sans-serif;
  font-weight: 700;
}

.pricing__price {
  font-size: 2.8rem;
  font-family: "Open Sans", sans-serif;
  font-weight: 300;
  margin-top: 25px;
  color: #FB3B64;
}

.pricing__limited {
  text-align: center;
  margin-top: 15px;
  font-size: 0.75rem;
  font-family: "Raleway", sans-serif;
  font-weight: 700;
}

.pricing__specs {
  text-align: center;
}

.pricing__option {
  font-size: 1rem;
  margin-bottom: 20px;
}

.pricing__option::before {
  content: "";
  width: 11px;
  height: 11px;
  display: inline-block;
  margin-right: 10px;
  background-image: url("../assets/Checkmark-1.svg");
}

.pricing__option--disabled::before {
  background-image: url("../assets/Close.svg");
}

.pricing__column .button {
  position: relative;
  bottom: -25px;
  font-size: 0.8rem;
  background-color: #FB3B64;
}

.pricing__column--1 {
  --accent-color: $color-redish;
  border: 1px solid #FB3B64;
}

.pricing__column--1 .pricing__price {
  margin-bottom: 30px;
}

.pricing__column--2 {
  --accent-color: $color-primary;
  width: 400px;
  margin: 0 40px;
  box-shadow: 5px 1px 27px rgba(7, 172, 230, 0.16);
  border: 1px solid #08A6E4;
}

.pricing__column--2 .pricing__plan {
  font-size: 1.3rem;
}

.pricing__column--2 .pricing__option {
  margin-bottom: 25px;
}

.pricing__column--3 {
  --accent-color: $color-green;
  border: 1px solid #55DFB4;
}

.button--left,
.button--right {
  width: 150px;
  height: 50px;
  border-radius: 23px;
}

.button--middle {
  box-shadow: 5px 10px 14px rgba(7, 172, 230, 0.16);
  background: #08A6E4 0% 0% no-repeat padding-box;
  width: 165px;
  height: 50px;
  border-radius: 23px;
}

.button {
  color: white;
  font-weight: bold;
  display: flex;
  border: none;
  justify-content: center;
  align-items: center;
}

@media (max-width: 960px) {
  .pricing {
    margin: 50px auto;
    flex-direction: column;
    width: 100%;
  }

  .pricing__columns {
    flex-direction: column;
  }
  .pricing__columns .pricing__column {
    width: 85%;
    margin: 20px 0 20px 0;
    flex-direction: row;
    flex-wrap: wrap;
  }

  .pricing__column:nth-child(2) {
    width: 95%;
  }

  .pricing__column .button {
    position: relative;
    bottom: -25px;
    left: 55px;
    font-size: 0.8rem;
    background-color: #FB3B64;
  }

  .pricing__top,
.pricing__specs {
    width: 50%;
  }

  .pricing__specs {
    text-align: left;
  }
}
/* contact */
.contact {
  padding: 80px 100px 100px;
  display: flex;
  justify-content: center;
}

.contact__column {
  width: 50%;
}

.contact__column--1 {
  margin-left: 150px;
}

.contact__column--2 {
  margin-right: 150px;
}

.contact__header {
  font-size: 3rem;
  line-height: 3.5rem;
}

.contact__text {
  margin: 22px 0 42px;
  font-size: 1rem;
  line-height: 1.375rem;
}

.contact__info {
  display: flex;
  justify-content: flex-start;
  color: #A5A5A5;
  align-items: center;
}

.details__email,
.details__telephone {
  color: #A5A5A5;
  font-weight: 500;
  text-decoration: none;
}

.details__email {
  margin-right: 20px;
}

.details__email img,
.details__telephone img {
  margin-right: 10px;
}

/* form */
.form {
  display: flex;
  flex-direction: column;
}

.form__field {
  display: flex;
  flex-direction: column;
}

.form__field:first-of-type {
  margin-bottom: 42px;
}

.form__label {
  font-weight: bold;
  text-transform: uppercase;
  color: #08A6E4;
  line-height: 0.9rem;
  letter-spacing: 3px;
  font-size: 0.5rem;
}

.form__input {
  border: none;
  border-bottom: 1px solid #A5A5A5;
  background-color: transparent;
  line-height: 2.625rem;
  border-bottom: 1px solid #A5A5A5;
}

.form__consent {
  margin-top: 30px;
}

.form__checkbox {
  display: block;
  position: relative;
  padding-left: 35px;
  margin-bottom: 12px;
  cursor: pointer;
  font-size: 1.35rem;
}

.form__checkbox input {
  position: absolute;
  opacity: 0;
  cursor: pointer;
  height: 0;
  width: 0;
}

.form__checkbox .checkbox {
  position: absolute;
  top: 0;
  left: 0;
  height: 25px;
  width: 25px;
  background-color: white;
  border: 1px solid #A5A5A5;
  border-radius: 4px;
}

.form__checkbox input:checked ~ .checkbox {
  background-image: url(../assets/Checkmark.svg);
  background-position: center;
  background-size: 50%;
  background-repeat: no-repeat;
}

.form__checkbox .consent__text {
  margin: 0 10px;
  font-size: 0.7rem;
  color: #A5A5A5;
  line-height: 17px;
}

.form .button--form {
  font-size: 0.8rem;
  padding: 20px 97px;
  align-self: flex-end;
  margin-top: 20px;
}

.button--form {
  width: 226px;
  height: 57px;
  background: #08A6E4 0% 0% no-repeat padding-box;
  box-shadow: 5px 10px 14px rgba(7, 172, 230, 0.16);
  border-radius: 35px;
}

@media (max-width: 1024px) {
  .contact {
    flex-direction: column;
    align-items: center;
    padding: 50px 15px;
  }

  .contact__column {
    width: 80%;
    margin: 0;
  }

  .contact__column--2 {
    margin-top: 30px;
    border-bottom: #F7F7F7 1px solid;
  }

  .details__telephone {
    display: flex;
  }
}
/* footer */
.footer {
  box-shadow: 0 -5px 40px #00000029;
  padding: 20px 0;
  display: flex;
  justify-content: space-around;
}

.footer__details {
  text-align: left;
  font: normal normal normal 0.5rem "Open Sans", sans-serif;
  letter-spacing: 0;
  color: #A5A5A5;
}

.twitter {
  padding: 0 10px 0 10px;
}

.facebook {
  padding: 0 10px 0 10px;
}

@media (max-width: 1024px) {
  .container {
    padding: 0;
  }

  .footer {
    width: 100%;
  }

  .twitter img {
    margin-top: 30px;
  }

  .facebook img {
    margin-top: 30px;
  }
}
